<template>
  <div class="choose-container">
    <div class="choose-bg">
      <img src="../assets/images/page_bg@2x.jpg" />
    </div>
    <div class="logo-img"><img src="../assets/images/logo@2x.png" /></div>
    <div class="camera-img"><img src="../assets/images/take_photo@2x.png" /></div>
    <div class="choose-button">
      <button @click="openAlbum(1)">上传照片</button>
      <button @click="openAlbum(2)">摄像头</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ChoosePhoto',
  data () {
    return {
    }
  },
  methods: {
    openAlbum (type) {
      // getWechatAuth = (type, openCameraForPic) => {
      // }
      // openCameraForPic(1)
      var sourceType = []
      if (type === 1) {
        sourceType.push('album')
      } else {
        sourceType.push('camera')
      }
      window.wx.chooseImage({
        count: 1, // 默认9
        sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
        sourceType: sourceType, // 可以指定来源是相册还是相机，默认二者都有
        success: function (res) {
          var localIds = res.localIds // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
          console.log(localIds)
          // uploadCallback.call(this, localIds)
        }
      })
      this.$router.push('/choosealbum')
    }
    // openCamera () {
    //   openCameraForPic(2)
    //   this.$router.push('/choosealbum')
    // }
  }
}
</script>

<style scoped lang="scss">
.choose-container {
  width: 100%;
  height: 100%;
  .choose-bg {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: -999;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .logo-img {
    position: absolute;
    top: 3rem;
    text-align: center;
    width: 100%;
    img {
      width: 150px;
    }
  }
  .camera-img {
    position: absolute;
    top: 180px;
    text-align: center;
    width: 100%;
    img {
      width: 150px;
    }
  }
  .choose-button {
    position: absolute;
    bottom: 100px;
    text-align: center;
    width: 100%;
    button {
      display: block;
      margin: auto;
      outline: none;
    }
    :first-child {
      margin-bottom: 20px;
    }
  }
}
</style>



